/**
 * ANNOUNCEMENTS JAVASCRIPT HANDLER
 *
 * @package
 * @since 4.0.0
 */

import AjaxRequest from './ajax-request';
import FieldsHandler from './fields-handler';


class YITH_Announcements {

	constructor() {
		this.init()
	}

	init() {
		jQuery( document ).on( 'click', 'a.page-title-action', {announcement: this}, this.create );
		jQuery( document ).on( 'click', 'button.save-announcement-modal', this.save );
		// Edit.
		jQuery( document ).on( 'click', '.yith-plugin-fw__action-button--edit-action, a.row-title', {announcement: this}, this.edit );
		// Enable.
		jQuery( document ).on( 'change', 'input[name="_announcement_enabled"]', this.active );
	}

	openModal( data ) {
		let template = wp.template( 'yith-wcmv-modal-announcement' ),
			templateButtons = wp.template(
				'yith-wcmv-modal-announcement-buttons'
			);

		yith.ui.modal( {
			width: 700,
			closeSelector: '.close-announcement-modal',
			classes: {
				wrap: 'announcement-modal',
				content: 'yith-plugin-ui',
			},
			title: yith_wcmv_announcements.modalTitle,
			content: template( data ),
			footer: templateButtons( {} ),
			onCreate() {
				const fields = new FieldsHandler( jQuery( 'form.announcement-modal-form' ) );
				fields.init();
			},
		} );
	}

	create( event ) {
		event.preventDefault();
		event.data.announcement.openModal(
			yith_wcmv_announcements.defaultModalValues
		);
	}

	edit( event ) {
		event.preventDefault();

		const handler = event.data.announcement;

		let row = jQuery( this ).closest( 'tr' ),
			announcement_id = row.find( '[name="post[]"]' ).val();

		if ( ! announcement_id ) {
			return false;
		}

		// Call must be unique. Abort the current one if processing
		AjaxRequest.abort();

		if ( row.data( 'announcement-data' ) ) {
			handler.openModal( row.data( 'announcement-data' ) );
		} else {
			AjaxRequest.call(
				{
					request: 'get_announcement_data',
					announcement_id,
				},
				jQuery( this ).closest( 'td' )
			).done( ( response ) => {
				if ( response.success ) {
					row.data( 'announcement-data', response.data );
					handler.openModal( response.data );
				}
			} );
		}
	}

	active( event ) {
		event.preventDefault();

		let active = jQuery( this ).is( ':checked' ) ? 'yes' : 'no',
			announcement_id = jQuery( this ).closest( 'tr' ).find( '[name="post[]"]' ).val();

		AjaxRequest.call(
			{
				request: 'announcement_active_switch',
				announcement_id,
				active
			},
			jQuery( this ).closest( 'td' ),
			'POST'
		);
	}

	save( event ) {
		event.preventDefault();
		jQuery( '.announcement-modal-form' ).submit();
	}
}

if ( typeof yith_wcmv_announcements !== 'undefined' ) {
	const announcement = new YITH_Announcements();
}
